/* Goggle Fonts */
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Sanchez&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arapey&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
/*   font-family: 'Dancing Script', cursive;
 */  font-family: 'Arapey', serif;
}

.App {
  text-align: center;
  height: 100vh;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("background3.jpg");
  background-repeat: no-repeat;
  background-size: cover;

}

.clock {
  position: relative;
  display:flex;
  justify-content: center;
  align-items: center;
  background: rgb(251, 192, 63);
  background: radial-gradient(circle, rgba(251, 192, 63, 0.8883928571428571) 21%, rgba(252, 238, 70, 0) 96%);

  border-radius: 70px;
  border: 2px solid silver;
  box-shadow: 0 0 5px 10px;
  color: rgba(8, 8, 1, 0.555);
  opacity: 0.9;
  
}

.clock section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  text-shadow: 1px 1px 2px whitesmoke;
  color: black;
}

.clock section p {
  font-size: 3rem;
  font-size: clamp(30px, 4vw, 50px);
  
}

.clock section small {
  color: silver;
  text-shadow: none;
  color: black;
  text-shadow: 1px 1px 7px whitesmoke;
  font-size: 1.5rem;
  font-size: clamp(15px, 4vw, 25px);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


footer {
  margin: 2rem;
  font-size: clamp(15px, 4vw, 25px);
  text-shadow: 1px 1px 1px black;
  color: whitesmoke;
}

.welcome {
  font-weight: 500;
  font-size: clamp(50px, 6vw, 100px);
  margin: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  word-break: keep-all;
  color: black;
  text-shadow: 1px 1px 2px whitesmoke;

}

.welcome_foldi_eden {
  word-break: keep-all;
  font-weight: 700;
  font-size: clamp(50px, 6vw, 100px);
  color: whitesmoke;
  text-shadow: 8px -2px 4px black;
}

.welcome-container {
  color: rgba(8, 8, 1, 0.555);
  box-shadow: 0 0 5px 10px;
  background: rgb(251, 192, 63);
  background: radial-gradient(circle, rgba(251, 192, 63, 0.8883928571428571) 21%, rgba(252, 238, 70, 0) 67%);
  border-radius: 15px;
  border: 2px solid silver;
  display: flexbox;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  opacity: 1;
}

.hamarosan_nyitunk_text {
  font-size: clamp(50px, 6vw, 100px);
  color: white;
  text-shadow: 8px -2px 4px black;
  margin: 1rem;
  padding-left: 0.2rem;
  padding-right: 0.4rem;
  background: rgb(251, 192, 63);
  background: radial-gradient(circle, rgba(251, 192, 63, 0.8883928571428571) 21%, rgba(252, 238, 70, 0) 67%);
  border-top: 2px solid silver;
  border-bottom: 2px solid silver;
  opacity: 0.7;
}
